*{
  box-sizing: border-box;
}

html{
  font-size: 16px;
}

body{
  margin: 0 auto;
  background-color: #E5E5E5;
  @include font;
}

img{
  display: inline-block;
}

@include header;

.text_title{
  font-size: rem(28px);
}

.text_desc{
  font-size: rem(16px);
  color: #7A7A7A;
}

.fifth_block{
  margin-top: rem(20px);
  display: block;
}

.fifth_flex_end{
  align-items: flex-end;
  h1{
    font-size: rem(28px);
    margin-bottom: 0;
  }
}

.hidden_menu_navigation{
  max-height: 0;
}

section{
  .container{
    &>div{
      padding: rem(40px) 0;
    }
    .first_block{
      @include flex;
      justify-content: space-between;
      &_inner{
        width: 30%;
        &_header{
          color: #015D9D;
        }
        &_desc{
          color: #7A7A7A;
          margin-top: rem(6px);
        }
      }
    }
    .second_block{
      @include flex;
      justify-content: space-between;
      &>div{
        width: 45%;
        height: rem(376px);
      }
      &_text{
        @include flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }
    }
    .fourth_block{
      &_inner{
        @include flex;
        justify-content: space-between;
        &>div{
          width: 45%;
          align-items: flex-end;
          &:last-child{
            &>div{
              .fifth_title{
                font-size: rem(13px) !important;
                color: #999999;
                margin-bottom: rem(20px);
              }
              &:first-child{
                font-size: rem(14px);
              }
              &:last-child{
                @include flex;
                justify-content: space-between;
                margin-top: rem(20px);
                h3{
                  font-size: rem(28px);
                }
                span{
                  font-size: rem(16px);
                }
              }
            }
          }
        }
      }
    }
    .seventh_block{
      &_top{
        text-align: center;
        h1{
          font-size: rem(38px);
        }
        p{
          font-size: rem(17px);
          color: #666666;
          margin: rem(20px) 0;
        }
      }
      &_bottom-list{
        &>div{
          margin: rem(42px) 0;
          @include flex;
          justify-content: space-between;
          h3{
            font-size: rem(16px);
            width: 30%;
          }
          p{
            width: 60%;
            color: #7A7A7A;
          }
        }
      }
    }
    .eight_block{
      @include flex;
      justify-content: space-between;
      &>div{
        width: 43%;
        h2{
          font-size: rem(28px);
        }
        h4{
          font-size: rem(18px);
        }
        p{
          color: #7A7A7A;
          font-size: rem(15px);
          margin-top: rem(40px);
        }
      }
    }
    .ninth_block{
      &_collapse_item{
        margin: rem(10px) 0;
        cursor: pointer;
        border: 1px solid #E7E7E7;
        overflow: hidden;
        transition: 0.4s all;
        max-height: 1000px;
        h2{
          font-size: rem(28px);
        }
      }
    }
  }
}

.container{
  padding: rem(17px) rem(64px);
}

@media screen and (max-width: 1023px) {
  section{
    .container{
      .fourth_block{
        &_inner{
          &>div{
            width: 100%;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 639px) {
  section{
    .container{
      .first_block{
        &_inner{
          width: 100%;
        }
      }
      .second_block{
        &>div{
          width: 100%;
          height: auto;
        }
