          margin: rem(10px) 0;
        }
      }
      .eight_block{
        &>div{
          width: 100%;
        }
      }
      .ninth_block{
        &_collapse_item{
          h2{
            font-size: rem(23px);
          }
        }
      }
    }
  }
}

