@import './helpers.scss';
@mixin header {
    header{
        background-repeat: no-repeat;
        background-size: cover;
        .container{
            
            .bottom_block{
                padding-top: rem(45px);
                padding-bottom: rem(80px);
                h1{
                    font-weight: 600;
                    color: white;
                    font-size: rem(48px);
                }
                div{
                    span{
                        color: $white;
                        font-size: rem(20px);
                        &:first-child{
                            text-decoration: underline;
                        }
                        &:last-child{
                            margin-left: rem(15px);
                        }
                    }
                }
            }
        }
    }
}